import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {GuiManagerProvider} from "../../providers/gui-manager/gui-manager";
import {StorageManagerProvider} from "../../providers/storage-manager/storage-manager";

/**
 * Generated class for the UserSettingsMessageCenterPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-settings-message-center',
  templateUrl: 'user-settings-message-center.html',
})
export class UserSettingsMessageCenterPage {

  private msgCenterBool = false;

  constructor(public navCtrl: NavController, public navParams: NavParams, private guiManager: GuiManagerProvider, private storageManager: StorageManagerProvider) {
    const me = this;
    storageManager.contains("msg-center", (cont) => {
      me.msgCenter = cont;
      guiManager.messageCenter = cont;
    })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad UserSettingsMessageCenterPage');
  }

  ionViewWillLeave() {

    const me = this;
    this.storageManager.contains("msg-center", (contains) => {
      if(contains) {
        if(!me.msgCenter) {
          me.storageManager.remove("msg-center", () => {
            me.guiManager.messageCenter = false;
          })
        }
      } else {
        if(me.msgCenter) {
          me.storageManager.save("msg-center", "",() => {
            me.guiManager.messageCenter = true;

          })
        }
      }
    })
  }

  get msgCenter() {
    return this.msgCenterBool;
  }

  set msgCenter(val) {
    this.msgCenterBool = val;
    this.guiManager.messageCenter = val;
  }

}



